.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

html,
body {
  font-family: "GotamPro", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  background-color: #CCCCCC;
  color: #222222;
  margin: 0;
  padding: 0;
  min-width: 320px; }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

a {
  text-decoration: none; }

.catalog__btn {
  width: 300px;
  height: 50px;
  margin: 0 auto;
  display: block;
  background-color: #1C69D4;
  border: none;
  color: #ffffff;
  border-radius: 4px;
  font-weight: 700;
  line-height: 23px;
  font-size: 16px; }

.header__wrapper {
  background-color: #ffffff; }

.header__wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.header__contact {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.header__link {
  text-decoration: none;
  color: #222222;
  font-weight: 700;
  font-size: 16px;
  line-height: 23px; }

.header__btn {
  border: none;
  background-color: #ffffff; }

.header__adress {
  position: relative;
  border: none;
  background-color: #CCCCCC;
  font-weight: 300; }

.header__tel {
  color: #222222;
  text-decoration: none;
  font-size: 18px;
  line-height: 19.6px; }

.slider {
  width: 100%;
  height: 338px;
  background-image: url(../../img/slider.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  margin: 0; }

.slider__item {
  font-size: 36px;
  line-height: 40px;
  color: #ffffff;
  font-weight: 700; }

.slider__arrow {
  margin-left: auto;
  width: 60px;
  height: 60px;
  border: none;
  background-color: rgba(255, 255, 255, 0.2); }

.container {
  width: 320px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .container {
      width: 768px; } }
  @media (min-width: 1300px) {
    .container {
      width: 1300px; } }

.navbar__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.navbar__btn {
  border: none;
  background-color: #ffffff; }
